import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import lodash from 'lodash'
import './assets/styles/postcss/index.scss'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.min.css'
// import ElementUi from 'element-ui'
// import 'element-ui/lib/theme-chalk/index.css'
import ElementPlus from 'element-plus';
import 'element-plus/theme-chalk/index.css';
import {echarts} from './util/echarats'
// Vue.config.devtools = true

const app = createApp(App)
console.log(
  document.querySelector("link[rel*='icon']"),
  document.getElementsByTagName('head')[0]
)
// document.getElementsByTagName('head')[0].removeChild(document.querySelector("link[rel*='icon']"))
Object.defineProperty(app.config.globalProperties, '_', {
  value: lodash,
})
app.config.globalProperties.routerAppend = (path, pathToAppend) => {
  return path + (path.endsWith('/') ? '' : '/') + pathToAppend
}
app.config.globalProperties.echarts = echarts
app.use(store)
app.use(router)
app.use(ElementPlus, VueAwesomeSwiper)
app.mount('#app')

